<template>
  <div class="hello">
    
    <!-- Logo -->
    <div width="100%">
      <img class="logo" src="../assets/model3.png">
    </div>
    <h1>Battery Pack</h1>
    <div>
      <md-radio v-model="battery" md-value="1">Standard Battery</md-radio>
      <md-radio v-model="battery" md-value="2">Long Range Battery</md-radio>
    </div>
    <h1>Color</h1>
    <div>
      <md-radio v-model="color" md-value="1">Solid Black</md-radio>
      <md-radio v-model="color" md-value="2">Midnight Silver Metallic</md-radio>
      <md-radio v-model="color" md-value="3">Deep Blue Metallic</md-radio>
      <br />
      <md-radio v-model="color" md-value="4">Silver Metallic</md-radio>
      <md-radio v-model="color" md-value="5">Pearl White Multi-Coat</md-radio>
      <md-radio v-model="color" md-value="6">Red Multi-Coat</md-radio>
    </div>
    <h1>Wheels</h1>
    <div>
      <md-radio v-model="wheels" md-value="1">Standard 18" Wheels</md-radio>
      <md-radio v-model="wheels" md-value="2">19" Sport Wheels</md-radio>
    </div>
    <h1>Options</h1>
    <div>
      <md-checkbox id="prem" value="prem" v-model="prem">Premium Upgrades</md-checkbox><br />
      <md-checkbox id="eap" value="eap" v-model="eap">Enhanced Autopilot</md-checkbox><br />
      <md-checkbox id="fsd" value="fsd" v-model="fsd">Full Self-Driving Capability</md-checkbox>
    </div>
    <h1>Currency</h1>
    <div>
      <md-radio v-model="currency" md-value="hkd">HKD</md-radio>
      <md-radio v-model="currency" md-value="use">USD</md-radio>
    </div>
    <md-bottom-bar class="bottom">
      <h1>Total Cost: ${{ cost }}</h1>
    </md-bottom-bar>
    </div>
</template>

<script>
export default {
  name: 'hello',
  data: () => ({
    battery: 1,
    color: 1,
    wheels: 1,
    prem: false,
    eap: false,
    fsd: false,
    currency: 'hkd'
  }),
  computed: {
    cost: function () {
      if (this.currency === 'hkd') {
        return this.hkd
      } else {
        return this.usd
      }
    },
    usd: function () {
      var batteryNum = 0
      if (this.battery > 1) { batteryNum = 9000 } else { batteryNum = 0 }
      var colorNum = 0
      if (this.color > 1) { colorNum = 1000 } else { colorNum = 0 }
      var wheelsNum = 0
      if (this.wheels > 1) { wheelsNum = 1500 } else { wheelsNum = 0 }
      var premNum = 0
      if (this.prem) { premNum = 5000 } else { premNum = 0 }
      var eapNum = 0
      if (this.eap) { eapNum = 5000 } else { eapNum = 0 }
      var fsdNum = 0
      if (this.fsd) { fsdNum = 3000 } else { fsdNum = 0 }
      return parseInt(35000) + parseInt(batteryNum) + parseInt(colorNum) + parseInt(wheelsNum) + parseInt(premNum) + parseInt(eapNum) + parseInt(fsdNum)
    },
    hkd: function () {
      var batteryNum = 0
      if (this.battery > 1) { batteryNum = 69900 } else { batteryNum = 0 }
      var colorNum = 0
      if (this.color > 1) { colorNum = 7800 } else { colorNum = 0 }
      var wheelsNum = 0
      if (this.wheels > 1) { wheelsNum = 11600 } else { wheelsNum = 0 }
      var premNum = 0
      if (this.prem) { premNum = 38800 } else { premNum = 0 }
      var eapNum = 0
      if (this.eap) { eapNum = 38800 } else { eapNum = 0 }
      var fsdNum = 0
      if (this.fsd) { fsdNum = 23300 } else { fsdNum = 0 }
      return parseInt(290500) + parseInt(batteryNum) + parseInt(colorNum) + parseInt(wheelsNum) + parseInt(premNum) + parseInt(eapNum) + parseInt(fsdNum)
    }
  }
}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>
.bottom-bar {
  margin-top: 100px
}

.logo {
  width: 50%
}

.battery {
  width: 40%;
  float: left;
  margin: 5%
}

.premium {
  width: 40%;
  float: left;
  margin: 5%
}

.bottom {
  position: fixed;
  bottom: 0px;
}
</style>



